.block {
    position: relative;
  height: 550px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.description{
    position: absolute;
    top: 50%;
    left:30px;
    transform: translateY(-50%);
    height: 500px;
    width: 550px;
    background-color: rgba(255,255,255,0.6);
    padding: 15px 22px;
    color: var(--text-color-1);
}
.title{
    font-size: 20px;
    margin-bottom: 70px;
}
.list{
    list-style: none;
    
}
.listItem{
    margin-bottom: 20px;
}